import React, { Component } from 'react';
import './Deal.scss';
import {
    withRouter
} from 'react-router-dom';
import asyncComponent from '../../utils/asyncComponent';
import { is, fromJS } from 'immutable';
import Iframe from 'react-iframe'
const LeftNav = asyncComponent(() => import("../../view/left_nav/LeftNav"));
const BuySell = asyncComponent(() => import("./buySell/BuySell"));

class Deal extends Component{
    state = {
        ifWidth: 0,
        ifHeight: 0
    };
    shouldComponentUpdate(nextProps, nextState) {
        return !is(fromJS(this.props), fromJS(nextProps))|| !is(fromJS(this.state),fromJS(nextState))
    };
    jumpToMarket = () => {
        this.props.history.push('/market')
    };
    render(){
        return(
            <div className="deal">
                <LeftNav currentPage={"deal"}/>
                <a id="select" onClick={this.jumpToMarket}>选择币种</a>
                <Iframe url="/kline/index.html"
                    width="100%"
                    height="100%"
                    id="myId"
                    className="myClassname"
                    display="initial"
                    position="relative"
                    allowFullScreen/>
                <BuySell />
            </div>
        )
    }
}

export default withRouter(Deal)


